import LoginAction from './action.types';

export const LoggingIn=(user)=>({
    type:LoginAction.LOGGINGIN,
    payload:user.accessToken
})

export const LoginSuccess=(user)=>({
    type:LoginAction.LOGINSUCCESS,
    payload:user["access-token"]
})

export const LoginFailure=(error)=>({
    type:LoginAction.LOGINFAILURE,
    payload:error.error
})

export const LogoutFailure=(error)=>({
    type:LoginAction.LOGOUTFAILED,
    payload:error.error
})
export const LogoutSuccess=()=>({
    type:LoginAction.LOGOUTSUCCESS
})
